import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of, throwError} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import {environment} from "../../../environments/environment";

export const API = `${environment.API_BASE}`;
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  }),
  // withCredentials: true
};

@Injectable({
  providedIn: 'root'
})
export class RevenueService {

  constructor(private http:HttpClient) { }
  summeryFinancial(){
    return this.http.get(`${API}/vredu/basket/financial`)
      .pipe(catchError(handleError<any>('Summery Financial')));
  }
  withDrawRequest(data){
    return this.http.post(`${API}/vredu/basket/withdraw`,data)
      .pipe(catchError(handleError<any>('withDrawRequest')));
  }
  withDrawRequestList(){
    return this.http.get(`${API}/vredu/basket/withdraw`)
      .pipe(catchError(handleError<any>('withDrawRequestList')));
  }
}
function handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {

    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead

    // TODO: better job of transforming error for user consumption
    // UtilitesService.log(`${operation} failed: ${error.message}`);

    return throwError(error.error);
  };
}
